	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Fluent 2014, \"Keynote With Paul Irish\".\n\nAbout Paul Irish (Google):\nPaul Irish is a front-end developer who loves the web. He is on Google Chrome's Developer Relations team as well as jQuery's.\n\nHe develops the HTML5 Boilerplate, the HTML5/CSS3 feature detection library Modernizr, HTML5 Please, CSS3 Please, and other bits and bobs of open source code.\n\nDon't miss an upload! Subscribe! http://goo.gl/szEauh\nStay Connected to O'Reilly Media by Email - http://goo.gl/YZSWbO\n\nFollow O'Reilly Media:\nhttp://plus.google.com/+oreillymedia\nhttps://www.facebook.com/OReilly\nhttps://twitter.com/OReillyMedia","meta":{"duration":"PT23M25S"},"presenter":{"name":null},"source":{"id":"R8W_6xWphtw","provider":"youtube"},"slug":"paul-irish-delivering-the-goods-fluent-2014-keynote","tag":["Paul Irish","fluent 2014","fluentconf","fluentcon","fluent conference","optimization","Engineering (Industry)","O'Reilly","O'Reilly Media","OReilly","OReilly Media","OReillyMedia"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/R8W_6xWphtw/default.jpg","height":90,"width":120}},"title":"Paul Irish, \"Delivering the goods\" - Fluent 2014 Keynote"}},{"node":{"description":"Don't miss an upload! Subscribe! http://goo.gl/szEauh\n\nHow does the pervasiveness of JavaScript on the client change how we architect applications? We can create hundreds virtual machines in the cloud, but we are using the millions of visual machines that visit our sites every day?\n\nSuddenly we are scripting against thousands of Virtual Machines from the command line while creating things today with JavaScript in the browser that were impossible yesterday. LiveScript becomes JavaScript becomes ES6 and now we're compiling C++ to JavaScript.\n\nJoin Scott Hanselman as he explores the relationship between the Cloud and the Browser, many Languages and one Language, how it might all fit together and what comes next.\n\nBuy Scott's books here. http://goo.gl/Y2Zsdh\n\nStay Connected to O'Reilly Media by Email - http://goo.gl/YZSWbO\n\nFollow O'Reilly Media:\nhttp://plus.google.com/+oreillymedia\nhttps://www.facebook.com/OReilly\nhttps://twitter.com/OReillyMedia","meta":{"duration":"PT25M57S"},"presenter":{"name":null},"source":{"id":"UzyoT4DziQ4","provider":"youtube"},"slug":"scott-hanselman-virtual-machines-javascript-and-assembler-fluent-2014-keynote","tag":["JavaScript","Assembly Language (Programming Language)","fluent 2014","fluentconf","fluent conference","Virtual Machine (Programming Language Paradigm)","scott hanselman","keynote","speaker","podcaster","Programming Language (Literary Genre)","o'reilly books","O'Reilly Media (Business Operation)","oreilly","oreilly media","O'Reilly"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/UzyoT4DziQ4/default.jpg","height":90,"width":120}},"title":"Scott Hanselman, \"Virtual Machines, JavaScript and Assembler\" -  Fluent 2014 Keynote"}},{"node":{"description":"http://oreilly.com/go/oscon-js-html5\n\nOnce again Damian Conway returns from the untamed wilderness of deepest Vim, bringing with him yet another collection of tips, tools, and tricks to make your text editing still more unspeakably powerful.\n\nDon't miss an upload! Subscribe! http://goo.gl/szEauh\nStay Connected to O'Reilly Media by Email - http://goo.gl/YZSWbO\n\nFollow O'Reilly Media:\nhttp://plus.google.com/+oreillymedia\nhttps://www.facebook.com/OReilly\nhttps://twitter.com/OReillyMedia","meta":{"duration":"PT38M51S"},"presenter":{"name":null},"source":{"id":"aHm36-na4-4","provider":"youtube"},"slug":"damian-conway-more-instantly-better-vim-oscon-2013","tag":["Vim (Software)","O'Reilly Open Source Convention (Conference Series)","Damian Conway","Thoughtstream","Mainstage","OSCON","Open Source Software (Industry)","programming tips","open source tips","open source tools","open source tricks","text editing","open source convention","O'Reilly Media (Business Operation)","tech convention","technology convention","tech conference","Technology (Professional Field)","O'Reilly","O'Reilly Media","OReilly","OReilly Media","OReillyMedia"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/aHm36-na4-4/default.jpg","height":90,"width":120}},"title":"Damian Conway, \"More Instantly Better Vim\" - OSCON 2013"}},{"node":{"description":"In my technical presentation (\"The Evolution of Java: Past, Present, and Future\"), I'll be discussing all of the changes to the Java programming language since its inception. In this this keynote, I'll focus my attention on the starting point: I'll present my candidates for the best and worst features in the platform as it was originally released (JDK 1.0), and explain the reasoning behind my choices. Some of my choices may be obvious, but others will likely surprise you. A splendid time is guaranteed for all.\n\nJosh Bloch\n\nGoogle\n\nJoshua Bloch is a software architect in the Open Source Program Office at Google, author of the bestselling, Jolt Award-winning \"Effective Java\" (Addison-Wesley, 2001; Second Edition, 2008), and coauthor of \"Java Puzzlers: Traps, Pitfalls, and Corner Cases\" (Addison-Wesley, 2005) and \"Java Conurrency in Practice\" (Addison-Wesley, 2006). He was previously a Distinguished Engineer at Sun Microsystems, where he led the design and implementation of numerous Java platform features including the Java Collections Framework and JDK 5.0 language enhancements. He holds a Ph.D. from CMU and a B.S. from Columbia.","meta":{"duration":"PT14M34S"},"presenter":{"name":null},"source":{"id":"hcY8cYfAEwU","provider":"youtube"},"slug":"oscon-java-2011-josh-bloch-java-the-good-bad-and-ugly-parts","tag":["oreilly","oreillymedia","oscon","opensource","java","data","perl","HTML5","android","mobile","conference","convention","O'Reilly","Tim O'Reilly","O'Reilly Media","OReilly","Tim OReilly","OReilly Media","OReillyMedia","o'reilly","books"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/hcY8cYfAEwU/default.jpg","height":90,"width":120}},"title":"OSCON Java 2011:  Josh Bloch, \"Java: The Good, Bad, and Ugly Parts\""}}]}},"pathContext":{"slug":"oreilly","tag":"O'Reilly"}}

